import React, { useState } from 'react';
import { flights } from '../data/flightsArray';
import { airports } from '../data/airportsArray';
import '../styles/browser.scss';

const Browser = () => {
  //Use state - przechwycenie warto≈õci z input√≥w
  const [fromValue, setFromValue] = useState("");
  const [toValue, setToValue] = useState("");

  //Stworzenie listy lotnisk z tablicy w pliku airportsArray.js
  let options = airports.map((val) => {
    return (
      <option key={val.id} value={val.value}>{val.name}</option>
    )
  });

  //Klasa Graph z funkcja addEdge odpowiadajƒÖcƒÖ za dodawanie 'wirtualnych' ga≈Çƒôzi grafu. 
  class Graph {
    constructor() {
      var neighbors = this.neighbors = {};

      this.addEdge = function (u, v) {
        
        if (neighbors[u] === undefined) {
          neighbors[u] = [];
        }
        neighbors[u].push(v);
        if (neighbors[v] === undefined) {
          neighbors[v] = [];
        }
        neighbors[v].push(u);
      };

      return this;
    }
  }

  // Funkcja ma jako argumenty klase Graph wraz z wszystkimi dodanymi krawƒôdziami,
  // miejsce wylotu i miejsce przylotu. Nastƒôpnie dziƒôki algorytmowi BFS(breadth-first search)
  // analizuje wszystkie krawƒôdzie grafu i zwraca tablice zawierajƒÖcƒÖ kolejne lotniska trasy.
  function shortestPath(graph, source, target) {
    //Walidacja input√≥w
    if(source === '' || target === '') {
      return print('Uzupe≈Çnij pola, aby≈õmy mogli pokazaƒá Ci mo≈ºliwe trasy!')
    } else if (source === target) {
      return print('Miejsca wylotu i przylotu sƒÖ takie same!');
    }

    document.getElementById('info').innerHTML = "Tak przedstawia siƒô Twoja trasa";
    document.getElementById('infoSecond').innerHTML = "Kup bilet na poszczeg√≥lne loty:";

    //Sortowanie
    var queue = [ source ],
        visited = { source: true },
        predecessor = {},
        tail = 0;
    while (tail < queue.length) {
      var u = queue[tail++],
          neighbors = graph.neighbors[u];
      for (var i = 0; i < neighbors.length; ++i) {
        var v = neighbors[i];
        if (visited[v]) {
          continue;
        }
        visited[v] = true;
        if (v === target) {
          var path = [ v ];
          while (u !== source) {
            path.push(u);
            u = predecessor[u];
          }
          path.push(u);
          path.reverse();

          let flightsNames = []

          for(let i = 0; i < path.length; i++) {
            for(let x = 0; x < airports.length; x++){
              if(path[i] === airports[x].value) {
                flightsNames.push(airports[x].name);
              }
            }
          }

          //Pokazanie trasy
          print(flightsNames.join(' &rarr; '));

          //Pokazanie poszczeg√≥lnych lot√≥w
          addFlight(flightsNames);
          return;
        }
        predecessor[v] = u;
        queue.push(v);
      }
    }
    print('there is no path from ' + source + ' to ' + target);
  }

  //Funkcja pokazujƒÖca na ekranie trase oraz wywo≈ÇujƒÖca animacje
  function print(s) {
    show();
    document.getElementById('display').innerHTML = "";
    s = s || '';
    document.getElementById('display').innerHTML += s + '<br>';
  }

  //Animacja wyjazdu
  function show() {
    document.getElementById('nearestPath').classList.add('display');
  }

  //Pokazanie poszczeg√≥lnych lot√≥w na trasie
  function addFlight(flightsNames){
    let div = document.getElementById('flightsContainer');
    

    //Zmiana, ≈ºeby w tablicy nie by≈Ço skr√≥t√≥w tylko pe≈Çne nazwy lotnisk
    

    div.innerHTML = '';

    //Wpisanie wszystkich poszczeg√≥lnych lot√≥w na ekranie.
    for(let i = 0; i < flightsNames.length - 1; i++) {
          div.innerHTML += `
          <div class="flight">
            <p class="flight__header">Lot nr. `+ parseInt(i + 1) +`</p>
            <p class="flight__infos">Miejsce wylotu</p>
            <p class="flight__infos">Miejsce przylotu</p>
            <p class="flight__way">`+ flightsNames[i] +`</p>
            <p class="flight__way">`+ flightsNames[i + 1] +`</p>
            <div class="flight__buttons">
              <p class="flight__button">Sprawd≈∫ hotele</p>
              <p class="flight__button">Kup bilet</p>
            </div>
          </div>
        `
    }
  }
  
  //Funkcja odpowiadajƒÖce za stworzenie krawƒôdzi grafu i wywo≈Çanie funkcji szukajƒÖcej ≈õcie≈ºki
  function checkFlights() {
    var graph = new Graph();

    //Pobrane loty z pliku flightsArray.js dodaje do funkcji tworzƒÖcej ga≈Çƒôzie grafu.
    for(let i in flights) {
      graph.addEdge(flights[i].pointA, flights[i].pointB);
    }

    shortestPath(graph, fromValue, toValue);
  };

  return (
    <>
      <div className="browserContainer">
        <div className="browser">
          <div className="browser__select">
            <label className="label">Miejsce wylotu</label>
            <select className="input" onChange={function(e) {setFromValue(e.target.value);}}>
              <option value="" disabled selected>Rozwi≈Ñ i wybierz</option>
              {options}
            </select>  
          </div>
          <div className="browser__select">
            <label className="label">Miejsce przylotu</label>
            <select className="input" onChange={function(e) {setToValue(e.target.value);}}>
                <option value="" disabled selected>Rozwi≈Ñ i wybierz</option>
                {options}
            </select>
          </div>
          <button className="browser__button" onClick={checkFlights}>Sprawdz</button>
        </div>
      </div>
      <div id="nearestPath" className="hide">
        <div className="path">
          <p id="info" className="path__info"></p>
          <div className="path__flights" id="display"></div>
          <p id="infoSecond" className="path__info"></p>
        </div>
        <div id="flightsContainer" className="flights"></div>
      </div>
    </>
  );
}

export default Browser;
